import React, { useEffect, useState } from "react";
import "./myskills.css";

const skills = [
	{
		skill: "JavaScript",
		percentage: 85,
		description: "JavaScript로 이 블로그를 만들었어용",
		modal_desc: "누르면 이거떴지롱ㅋㅅㅋ",
	},
	{
		skill: "Python",
		percentage: 80,
		description: "파이썬도 배웠어용ㅎㅅㅎ",
		modal_desc: "이거는 파이썬이라고 띄울건데ㅋㅋ",
	},
	{
		skill: "React",
		percentage: 75,
		description: "이 블로그는 리액트로 만들어짐 ㄷㄷㄷ",
		modal_desc: "요건 리액트ㅋㅋㅋ",
	},
	{
		skill: "Node.js",
		percentage: 70,
		description: "이건 챗지피티가 적으래서 적은거예요",
		modal_desc: "이건 지울거라 제대로 안할거지롱ㅋㅋㅋ",
	},
];

const MySkills = () => {
	return (
		<div className="skills-container">
			<h1>My Skills</h1>
			<div className="skills-grid">
				{skills.map((skill, index) => (
					<SkillProgress
						key={index}
						skill={skill.skill}
						targetPercentage={skill.percentage}
						description={skill.description}
						modal_desc={skill.modal_desc}
					/>
				))}
			</div>
		</div>
	);
};

const SkillProgress = ({
	skill,
	targetPercentage,
	description,
	modal_desc,
}) => {
	const [percentage, setPercentage] = useState(0);
	const [modalOpen, setModalOpen] = useState(false);

	useEffect(() => {
		const timer = setTimeout(() => {
			if (percentage < targetPercentage) {
				setPercentage(percentage + 1);
			}
		}, 5);
		return () => {
			clearTimeout(timer);
		};
	}, [percentage, targetPercentage]);

	const radius = 40;
	const circumference = 2 * Math.PI * radius;
	const strokeDashoffset = circumference - (percentage / 100) * circumference;

	const skillClass = skill.toLowerCase().replace(/[^a-z]/g, "-");

	return (
		<div className={`skill-progress ${skillClass}`}>
			<div className="skill-info">
				<svg width="100" height="100" viewBox="0 0 100 100">
					<circle
						cx="50"
						cy="50"
						r={radius}
						strokeWidth="10"
						stroke="#eee"
						fill="none"
					/>
					<circle
						className="progress-ring"
						cx="50"
						cy="50"
						r={radius}
						strokeWidth="10"
						strokeLinecap="square"
						fill="none"
						strokeDasharray={circumference}
						strokeDashoffset={strokeDashoffset}
						transform="rotate(-90 50 50)"
					/>
				</svg>
				<div className="skill-text">
					<h3
						className="skill-name"
						onClick={() => setModalOpen(true)}>
						{skill}
					</h3>
					<p className="skill-desc">{description}</p>
				</div>
			</div>
			{modalOpen && (
				<div className="modal">
					<div className="modal-content">
						<h3>{skill}</h3>
						<p>{modal_desc}</p>
						<button onClick={() => setModalOpen(false)}>
							Close
						</button>
					</div>
				</div>
			)}
		</div>
	);
};

export default MySkills;
